"use strict"

 import { HOMEONE ,HOMETWO, HOMETHREE ,HOMEFOUR} from '@/config/image.js';
 import simpleAddress from "@/components/simple-address/simple-address.nvue"
 import uniPopup from "@/components/uni-popup/uni-popup.vue"
 
 

var _self;
export default {
	props: {
		dataSix: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	components: {
	            simpleAddress,
				uniPopup
	        },
	
	data() {
		return {
			
			homeData:[
				{ img: HOMEONE, name: "地中海风格" ,value:'ONEHOME'},
				{ img: HOMETWO, name: "现代简约" ,value:'TWOHOME'},
				{ img: HOMETHREE, name: "新中式风格"  ,value:'THREEHOME'},
				{ img: HOMEFOUR, name: "欧式风格"  ,value:'OTHERHOME'},
			],
			current:null,
			cityPickerValueDefault: [0, 0, 1],
			                pickerText: '北京市 市辖区 西城区',
							agree:false
		}
	},
	methods: {
		homeClick(e,c) {
			// console.log(_self)
			_self.current =e
			this.$emit('testOne',c,8,this.dataSix.tid)
			},
			agre() {
				_self.agree=!this.agree
			},
			openAddres() {
				
			                this.$refs.simpleAddress.open();
			            },
			            onConfirm(e) {
							
							var b=e.label.split('-')
							// console.log(e)
							
			                this.pickerText = b[0]+' '+ b[1]+' '+b[2]
			            },
			open(){
			         this.$refs.popup.open()
			      },
			cancel(e) {
				if(e == 'ok'){
					this.agree=true
				}
				if(e == 'no'){
					this.agree=false
				}
				this.$refs.popup.close()
			},
			change(e) {
				console.log('是否打开:' + e.show)
			},
			submit:function () {
				this.$emit('submit')
				
				
			}
	    
	},
	created(){
		_self = this
		
	},
	
}
